 header{
    position:fixed;
    top:0; left:0; right:0;
    background:#fff;
    z-index: 1000;
    padding:1rem 9%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    box-shadow: 0 .5rem 1rem rgba(0,0,0,.1);
    margin-bottom: 0;
    padding-bottom: 0;
  }
   
  .navbar{
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .navbar a{
    list-style: none;
    padding: 0 20px;
    position: relative;
    
  
  }
  .navbar a{
    text-decoration: none;
    font-size:1.3rem;
    font-weight: 700;
    color: #2D2D2D;
    transition: 0.3s ease;
  }
  .navbar  a:hover,
  .navbar  a.active{
    color:#40867b;
  
  }
  .navbar a.active::after,
  .navbar a:hover::after{
    content: "";
    width: 30%;
    height: 2px;
    background: #40867b;;
    position: absolute;
    bottom: -4px;
    left: 20px;
  }
  
  header .icons a{
    font-size: 2.5rem;
    color:var(--MAIN);
    margin-left:1.5rem;
  }
  
  header .icons a:hover{
    color:var(--HOVER);
  }